// components/FriendActionModal.tsx
import React, { useEffect, useState } from "react";
import "../../styles/userService/ModalFriends.module.css"
import {
  getFriendshipStatus,
  sendFriendRequest,
  acceptFriendRequest,
  rejectFriendRequest,
  deleteFriend,
  blockUser,
  unblockUser,
} from "@/api/userService/friends";
import type { FriendsResDTO } from "@/types/userService/friends";

interface FriendActionModalProps {
  currentUserSignId: string;        // ë¡œê·¸ì¸í•œ ì‚¬ìš©ì
  targetUserSignId: string;
  targetUserNickname: string;
  isOpen: boolean;
  onClose: () => void;
  onSuccess?: () => void;
}

export default function FriendActionModal({
                                            currentUserSignId,
                                            targetUserSignId,
                                            targetUserNickname,
                                            isOpen,
                                            onClose,
                                            onSuccess,
                                          }: FriendActionModalProps) {
  const [status, setStatus] = useState<FriendsResDTO | null>(null);
  const [loading, setLoading] = useState(false);

  const fetchStatus = async () => {
    if (!isOpen) return;
    setLoading(true);
    try {
      const data = await getFriendshipStatus(currentUserSignId, targetUserSignId);
      setStatus(data);
    } catch (err) {
      setStatus(null);
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    if (isOpen) fetchStatus();
  }, [isOpen, targetUserSignId]);

  const handle = async (action: () => Promise<any>) => {
    setLoading(true);
    try {
      await action();
      await fetchStatus();
      onSuccess?.();
    } catch (err: any) {
      alert(err.message || "ì‘ì—… ì‹¤íŒ¨");
    } finally {
      setLoading(false);
    }
  };

  if (!isOpen) return null;

  const isMeBlocked = status?.friendsStatus === "BLOCKED" && status.requesterSignId === currentUserSignId;
  const isPendingFromMe = status?.friendsStatus === "PENDING" && status.requesterSignId === currentUserSignId;
  const isPendingToMe = status?.friendsStatus === "PENDING" && status.receiverSignId === currentUserSignId;
  const isFriend = status?.friendsStatus === "ACCEPTED";

  return (
      <div className="modalBackdrop" onClick={onClose}>
        <div className="modalContent" onClick={(e) => e.stopPropagation()}>
          <h2 className="modalTitle">
            {isFriend ? "ì¹œêµ¬" : isMeBlocked ? "ì°¨ë‹¨ë¨" : isPendingToMe ? "ì¹œêµ¬ ìš”ì²­" : "ì¹œêµ¬ ì¶”ê°€"}
          </h2>

          {loading && !status ? (
              <p className="modalMessage">ë¶ˆëŸ¬ì˜¤ëŠ” ì¤‘...</p>
          ) : (
              <>
                {/* ì°¨ë‹¨ë¨ */}
                {isMeBlocked && (
                    <>
                      <p className="modalMessage">
                        <strong>{targetUserNickname}</strong>ë‹˜ì„ ì°¨ë‹¨í–ˆìŠµë‹ˆë‹¤.
                      </p>
                      <div className="modalActions">
                        <button className="cancelButton" onClick={onClose}>ë‹«ê¸°</button>
                        <button
                            className="deleteButton"
                            disabled={loading}
                            onClick={() => handle(() => unblockUser(currentUserSignId, status!.friendId))}
                        >
                          {loading ? "í•´ì œ ì¤‘..." : "ì°¨ë‹¨ í•´ì œ"}
                        </button>
                      </div>
                    </>
                )}

                {/* ë‚´ê°€ ë°›ì€ ìš”ì²­ */}
                {isPendingToMe && (
                    <>
                      <p className="modalMessage">
                        <strong>{targetUserNickname}</strong>ë‹˜ì´ ì¹œêµ¬ ìš”ì²­ì„ ë³´ëƒˆìŠµë‹ˆë‹¤.
                      </p>
                      <div className="modalActions">
                        <button
                            className="cancelButton"
                            disabled={loading}
                            onClick={() => handle(() => rejectFriendRequest(currentUserSignId, status!.friendId))}
                        >
                          ê±°ì ˆí•˜ê¸°
                        </button>
                        <button
                            className="deleteButton"
                            disabled={loading}
                            onClick={() => handle(() => acceptFriendRequest(currentUserSignId, status!.friendId))}
                        >
                          ìˆ˜ë½í•˜ê¸°
                        </button>
                      </div>
                    </>
                )}

                {/* ë‚´ê°€ ë³´ë‚¸ ìš”ì²­ */}
                {isPendingFromMe && (
                    <>
                      <p className="modalMessage">
                        <strong>{targetUserNickname}</strong>ë‹˜ì—ê²Œ ì¹œêµ¬ ìš”ì²­ì„ ë³´ëƒˆìŠµë‹ˆë‹¤.
                      </p>
                      <div className="modalActions">
                        <button className="cancelButton" onClick={onClose}>ë‹«ê¸°</button>
                      </div>
                    </>
                )}

                {/* ì¹œêµ¬ ìƒíƒœ */}
                {isFriend && (
                    <>
                      <p className="modalMessage">
                        <strong>{targetUserNickname}</strong>ë‹˜ê³¼ ì¹œêµ¬ì…ë‹ˆë‹¤.
                      </p>
                      <div className="modalActions">
                        <button
                            className="cancelButton"
                            disabled={loading}
                            onClick={() => handle(() => deleteFriend(currentUserSignId, status!.friendId))}
                        >
                          ì¹œêµ¬ ëŠê¸°
                        </button>
                        <button
                            className="deleteButton"
                            disabled={loading}
                            onClick={() => handle(() => blockUser(currentUserSignId, targetUserSignId))}
                        >
                          ì°¨ë‹¨í•˜ê¸°
                        </button>
                      </div>
                    </>
                )}

                {/* ê´€ê³„ ì—†ìŒ */}
                {!status && (
                    <>
                      <p className="modalMessage">
                        <strong>{targetUserNickname}</strong>ë‹˜ì—ê²Œ<br />ì¹œêµ¬ ìš”ì²­ì„ ë³´ë‚´ì‹œê² ìŠµë‹ˆê¹Œ?
                      </p>
                      <div className="modalActions">
                        <button className="cancelButton" onClick={onClose}>ì·¨ì†Œ</button>
                        <button
                            className="deleteButton"
                            disabled={loading}
                            onClick={() => handle(() => sendFriendRequest(currentUserSignId, targetUserSignId))}
                        >
                          {loading ? "ë³´ë‚´ëŠ” ì¤‘..." : "ì¹œêµ¬ ìš”ì²­"}
                        </button>
                      </div>
                    </>
                )}
              </>
          )}
        </div>
      </div>
  );
}